[data-modal] {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  min-width: 320px;
  @include vertical;
  @include gpu;
  overflow: auto;
  transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
  background-color: rgba($black, 0.7);
  padding: 15px; }

[data-modal] {
  &.is-open {
    opacity: 1;
    visibility: visible;
    z-index: 100; } }

[data-modal-container] {
  position: relative;
  z-index: 1;
  vertical-align: middle;
  display: inline-block;
  margin: 0 auto; }

[data-modal-close] {
  position: absolute;
  @include circle(70);
  background-color: $blue;
  font-size: 0;
  right: -24px;
  bottom: -24px;
  transition: opacity $tr;
  box-shadow: 0 13px 18px rgba(0, 0, 0, 0.42);
  @include vertical;
  @include sm {
    @include circle(20); }
  @include hover {
    .icon {
      transform: scale(1.2); } }
  .icon {
    display: inline-block;
    vertical-align: middle;
    font-size: 24px;
    color: $white;
    transform-origin: 50% 50%;
    transition: transform $tr;
    @include sm {
      font-size: 10px; } } }

.modal {
  &__container {
    max-width: 600px;
    width: 100%;
    padding: 30px 30px 26px 36px;
    box-shadow: 0 8px 22px rgba(0, 0, 0, 0.37);
    border-radius: 10px;
    background-color: $white;
    @include sm {
      max-width: 300px; } }
  &__title {
    text-align: left;
    margin-bottom: 44px;
    @include sm {
      margin-bottom: 20px; } }
  &__text {
    // color: rgba($mainTextColor, 0.5)
    // font-family: $montserrat
    font-size: 12px;
    font-weight: 600;
    line-height: 1.883;
    @include sm {
      line-height: 1.833; } } }

