  font-size: 12px;
  font-weight: 600;
  line-height: 1.883;
}

@media only screen and (max-width: 767px) {
  .modal__text {
    line-height: 1.833;
  }
}

.sidebar {
  padding-right: 30px;
}

.sidebar__bottom {
  padding-top: 30px;
}

.links {
  line-height: 1.33;
  letter-spacing: 0.04em;
}

.links__item:not(:first-child) {
  margin-top: 9px;
}

.links__item a {
  color: currentColor;
  opacity: 0.5;
  transition: opacity .3s;
}

.no-touch .links__item a:hover {
  opacity: 1;
}

.fancy-bottom {
  position: absolute;
  padding: 13px 0;
  top: 100%;
  left: 0;
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
  color: #fff;
  font-size: 13px;
  font-family: "AvenirNext", Helvetica, sans-serif;
}

.fancy-bottom__link {
  display: block;
  color: #fff;
  font-size: 13px;
}

.fancy-bottom__right {
  display: flex;
}

.fancy-bottom__ref {
  position: relative;
}

.fancy-bottom__ref:not(:first-child) {
  padding-left: 33px;
}

.fancy-bottom__ref:not(:first-child):before {
  content: '';
  position: absolute;
  height: 14px;
  width: 1px;
  top: 50%;
  left: 16px;
  transform: translateY(-50%);
  background-color: rgba(216, 216, 216, 0.4);
}

.fancy-close {
  position: absolute;
  top: -47px;
  right: -50px;
  cursor: pointer;
}

.fancybox-slide {
  padding: 0 55px;
}

.fancybox-navigation .fancybox-button {
  background: none;
  width: 26px;
  height: 40px;
}
